.popup__input {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    max-width: 358px;
    width: 100%;
    margin: 0 auto 30px;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom-color: rgba(84, 84, 84, 0.2);
}

@media screen and (max-width: 640px) {
    .popup__input {
        max-width: 206px;
    }
}